fpom .nc::before {
  content: "NEED CONTENT \a";
  font-size: inherit !important;
}

.li::before {
  content: "FPO \a";
  font-size: inherit !important;
}

.nc::before {
  content: "NEED CONTENT \a";
  font-size: inherit;
}

.nc::before,
.li::before {
  font-size: inherit !important;
  font-weight: bold;
  color: #f0f;
  white-space: pre-wrap;
}

.li,
.nc {
  padding: 5px;
  background-color: rgba(221, 0, 221, 0.1333333333);
  border-radius: 5px;
  font-size: inherit !important;
}

.mono {
  font-family: monospace;
}

pre {
  page-break-inside: avoid;
  display: block;
  overflow: auto;
  max-width: 100%;
  margin-bottom: 1.6em;
  padding: 1em 1.5em;
  background: #f4f4f4;
  border: 1px solid #ddd;
  border-left: 3px solid #880088;
  font-size: 15px;
  font-family: monospace;
  line-height: 1.6;
  color: #666;
  word-wrap: break-word;
}

.white {
  color: white !important;
}

img.sample,
img.placeholder,
img.missing {
  background-color: lightgray;
}

.fpo-image {
  background-image: url("https://dev.feralcc.com/media/fpo.png");
  background-size: repeat;
}

.fpo-overlay::before {
  content: "FPO";
  position: relative;
  z-index: 999;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 10vh;
  font-weight: 700;
  line-height: 1em;
  color: rgba(255, 0, 255, 0.4);
  text-align: center;
}

body:has(.viewport-widget) {
  padding-top: 1.5em !important;
}

.viewport-widget {
  width: 100vw !important;
  margin: 0 !important;
  padding: 0 !important;
  position: fixed !important;
  display: flex !important;
  align-items: center;
  justify-content: center;
  left: 0 !important;
  z-index: 100 !important;
  opacity: 0.9 !important;
  height: 1rem;
  min-height: 1rem;
}
.viewport-widget.top {
  top: 0 !important;
}
.viewport-widget.bottom {
  bottom: 0 !important;
}

.viewport-size {
  font-family: monospace;
  font-size: 0.75rem;
  line-height: 1em;
  color: white;
  font-weight: 400;
  align-items: center;
  justify-content: center;
  display: flex;
  text-align: center;
  width: 100vw !important;
  height: 1rem;
  min-height: 1rem;
  padding: 0;
}

/* X-Small (xs) - <576px */
@media (max-width: 575px) {
  .viewport-size {
    background-color: #f44336;
    color: black !important;
  }
  .viewport-size:after {
    content: "XS (<576px)";
    font-weight: 600;
  }
}
/* Small (sm) - ≥576px */
@media (min-width: 576px) and (max-width: 767.98px) {
  .viewport-size {
    background-color: #ff9800;
    color: black !important;
  }
  .viewport-size:after {
    content: "SM (≥576px)";
    font-weight: 600;
  }
}
/* Medium (md) - ≥768px */
@media (min-width: 768px) and (max-width: 991.98px) {
  .viewport-size {
    background-color: #ffeb3b;
    color: black !important;
  }
  .viewport-size:after {
    content: "MD (≥768px)";
    font-weight: 600;
  }
}
/* Large (lg) - ≥992px */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .viewport-size {
    background-color: #4caf50;
    color: white;
  }
  .viewport-size:after {
    content: "LG (≥992px)";
    font-weight: 600;
  }
}
/* Extra Large (xl) - ≥1200px */
@media (min-width: 1200px) and (max-width: 1399.98px) {
  .viewport-size {
    background-color: #2196f3;
    color: black !important;
  }
  .viewport-size:after {
    content: "XL (≥1200px)";
    font-weight: 600;
  }
}
/* Extra Extra Large (xxl) - ≥1400px */
@media (min-width: 1400px) {
  .viewport-size {
    background-color: #9c27b0;
    color: white;
  }
  .viewport-size:after {
    content: "XXL (≥1400px)";
    font-weight: 600;
  }
}
