}

/*| BOOTSTRAP COLUMN WIDGET */
.grid-widget {
  position: fixed !important;
  z-index: 999 !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  min-height: 100vh !important;
  margin: auto !important;
}

.grid-widget .grid-column {
  width: calc(8.33vw - 27.5px);
  min-height: 100vh !important;
  margin: 0 15px;
  padding: 0;
  padding-top: 50px !important;
  background-color: rgba(255, 0, 255, 0.1333333333);
  border: 0.5px solid #ff00ff;
  font-size: 2vw !important;
  font-weight: 500 !important;
  color: rgba(255, 0, 255, 0.6);
  text-align: center !important;
}

.grid-widget .grid-column:first-child {
  margin: 0 15px 0 0;
}

.grid-widget .grid-column:last-child {
  margin: 0 0 0 15px;
}

